.checkoutProduct{
    display: flex;
    margin-top: 20px;
    box-shadow: 2px 2px 10px 2px rgba(0, 0, 0, 0.5);
    padding: 10px;
}
.checkoutProduct__image{
    object-fit: contain;
    width: 180px;
    height: 180px;
}

.checkoutProduct__title{
font-size: 17px;
font-weight: 600;
}


.amazon-image-ad{
    width: 100%;
    height: 200px;
    background-color: red;
}


.checkoutProduct__info{
    padding-left: 20px;
}
.checkoutProduct__rating{
    display: flex;
}
.checkoutProduct__info > button{
    margin-right: 10px;
    background-color: #f0c24b;
    border: 1px solid;
    margin-top: 10px;
    border-color:orange;
    color: black;
    padding: 5px 8px;
    cursor: pointer;
    font-weight: 700;
}


.checkout-bottom{
    max-width: 300px;
    text-align: center;
    position: absolute;
    bottom: 10%;
    left: 40%;
}
.checkout-bottom button{
    width: 100%;
    padding: 8px 10px;
    margin-top: 10px;
    margin-bottom: 10px;
    font-weight: 500;
    cursor: pointer;
    letter-spacing: 1.2px;
    background-color: #f0c24b;
    border: none;
}