.product{
    display: flex;
 flex-direction: column;


 
 align-items: center;
 max-height: 400px;
 min-width: 100px;
 background-color: whitesmoke;
 margin: 10px;
 padding: 20px;
 z-index: 10px;
}



.productssss{
    display: flex;
}
.product__info{
    height: 100px;
    margin-bottom: 15px;
}

.product > img{
  width: 100%;
max-height: 200px;  
object-fit: contain;
margin-bottom: 15px;
}

.product__price{
    margin-top: 5px;
}
.product__rating{
    display: flex;
}

.product > button{
    width: 100%;
    padding: 8px 10px;
    margin-top: 10px;
    margin-bottom: 10px;
    font-weight: 500;
    cursor: pointer;
    letter-spacing: 1.2px;
    background-color: #f0c24b;
    border: none;

}